html {
  font-size: 100%;
  font-family: "Spartan", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
}

ol,
ul {
  list-style: none;
}

img {
  pointer-events: none;
  user-select: none;
}

.center, .filter-box, .filter-box .filters .filter, .job__tag--name, .job__tag--new, .job__tag--featured {
  display: flex;
  align-items: center;
  justify-content: center;
}
.center--horizontal, .filter-box .filters, .job__tag, .job__detail, .job__filter {
  display: flex;
  align-self: center;
  justify-content: flex-start;
}

.filter-tag, .filter-box .filters .filter__name, .job__filter > div {
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  padding: 8px;
  border-radius: 2px;
  font-size: 0.8125rem;
  font-weight: 600;
  color: #669C9A;
  background-color: #F1F7F7;
  cursor: pointer;
}

header {
  width: 100%;
  height: 9.75rem;
}
@media (max-width: 46.1875em) {
  header {
    background: url(../../../images/bg-header-mobile.svg) #5DA5A4 no-repeat;
    background-size: cover;
  }
}
@media (min-width: 46.25rem) {
  header {
    background: url(../../../images/bg-header-desktop.svg) #5DA5A4 no-repeat;
    background-size: cover;
  }
}

.filter-box {
  justify-content: space-between;
  background-color: #FFFFFF;
  border-radius: 4px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  margin-top: -5.125rem;
  transition: 0.6s ease-in;
  display: none;
}
@media (max-width: 46.1875em) {
  .filter-box {
    padding: 1.25rem;
    width: 87.2%;
  }
}
@media (min-width: 46.25rem) {
  .filter-box {
    padding: 1.25rem 2.5rem;
    width: 77.0833333333%;
    min-width: 44.25rem;
    max-width: 69.375rem;
  }
}
.filter-box .filters {
  flex-wrap: wrap;
  gap: 16px;
}
.filter-box .filters .filter {
  cursor: pointer;
  background-color: #f1f7f7;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
}
.filter-box .filters .filter__name {
  box-shadow: unset;
}
.filter-box .filters .filter__times {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border-radius: 0 4px 4px 0;
  background-color: #5FA49F;
  transition: 0.4s ease;
  position: relative;
}
.filter-box .filters .filter__times:hover {
  background-color: #2b3b3a;
}
.filter-box .filters .filter__times::before {
  position: absolute;
  content: "";
  height: 4px;
  width: 20px;
  background-color: white;
  transform: rotate(45deg);
  left: 6px;
  top: 16px;
  border-radius: 2px;
}
.filter-box .filters .filter__times::after {
  position: absolute;
  content: "";
  height: 4px;
  width: 20px;
  background-color: white;
  transform: rotate(135deg);
  left: 6px;
  top: 16px;
  border-radius: 2px;
}
.filter-box .clear {
  cursor: pointer;
  font-weight: 600;
  font-size: 12px;
  color: #759395;
  position: relative;
}
.filter-box .clear:hover > .clear-hover {
  display: block;
  width: 100%;
}
.filter-box .clear-hover {
  position: absolute;
  bottom: 0;
  height: 1.5px;
  width: 0%;
  transition: 0.2s ease-in;
  background-color: #859592;
}

main {
  background-color: #f0fafb;
  padding: 3.375rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (max-width: 46.1875em) {
  main {
    gap: 40px;
  }
}
@media (min-width: 46.25rem) {
  main {
    gap: 24px;
  }
}

.job {
  background-color: #fff;
  position: relative;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.4);
  border-radius: 4px;
  transition: 0.4s ease;
}
.job:hover {
  transform: translateY(-12px);
}
@media (max-width: 46.1875em) {
  .job {
    padding: 1.25rem;
    width: 87.2%;
  }
}
@media (min-width: 46.25rem) {
  .job {
    padding: 2.125rem;
    width: 77.0833333333%;
    min-width: 44.25rem;
    max-width: 69.375rem;
  }
}
.job.featured {
  border-left: 5px solid #5BA5A4;
}
.job__logo {
  display: block;
}
@media (max-width: 46.1875em) {
  .job__logo {
    width: 3rem;
    margin-top: -2.625rem;
  }
}
@media (min-width: 46.25rem) {
  .job__logo {
    width: 5.5625rem;
    margin-right: 1.5rem;
  }
}
.job__tag {
  flex-wrap: wrap;
}
@media (max-width: 46.1875em) {
  .job__tag {
    margin-top: 0.375rem;
  }
}
.job__tag--name {
  font-weight: 600;
  color: #ABC9CB;
  margin-right: 1rem;
}
@media (max-width: 46.1875em) {
  .job__tag--name {
    font-size: 0.8125rem;
  }
}
@media (min-width: 46.25rem) {
  .job__tag--name {
    font-size: 0.875rem;
  }
}
.job__tag--new, .job__tag--featured {
  font-weight: 600;
  border-radius: 40px;
  color: #eaf8fa;
  text-transform: uppercase;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.4);
  background-color: #5ca6a9;
  padding: 0.25rem 0.5rem;
}
@media (max-width: 46.1875em) {
  .job__tag--new, .job__tag--featured {
    font-size: 0.625rem;
    margin-right: 0.75rem;
  }
}
@media (min-width: 46.25rem) {
  .job__tag--new, .job__tag--featured {
    font-size: 12px;
    font-weight: 500;
    margin-right: 0.5rem;
  }
}
.job__tag--featured {
  background-color: #2B3B3A;
  padding: 0.375rem 0.5rem;
}
@media (max-width: 46.1875em) {
  .job__tag--featured {
    font-size: 0.6875rem;
  }
}
@media (min-width: 46.25rem) {
  .job__tag--featured {
    font-size: 0.625rem;
  }
}
.job__position {
  font-weight: 600;
}
@media (max-width: 46.1875em) {
  .job__position {
    margin-top: 0.75rem;
    margin-bottom: 1.125rem;
    font-size: 0.9375rem;
  }
}
@media (min-width: 46.25rem) {
  .job__position {
    font-size: 1.125rem;
  }
}
.job__detail {
  gap: 24px;
}
@media (max-width: 46.1875em) {
  .job__detail {
    margin-bottom: 1.125rem;
  }
}
.job__detail > div:not(:last-child):before {
  content: "";
  position: absolute;
  height: 4px;
  width: 4px;
  background-color: #c2c2c2;
  top: 50%;
  transform: translateY(-50%);
  right: -16px;
  border-radius: 50%;
}
.job__detail > div {
  font-size: 0.75rem;
  font-weight: 400;
  position: relative;
  color: #868C8C;
}
@media (max-width: 46.1875em) {
  .job__separate {
    height: 2px;
    width: 100%;
    background-color: #C0C2C1;
  }
}
@media (min-width: 46.25rem) {
  .job__separate {
    display: none;
  }
}
.job__filter {
  gap: 16px;
  flex-wrap: wrap;
}
@media (max-width: 46.1875em) {
  .job__filter {
    margin-top: 1rem;
  }
}
@media (min-width: 46.25rem) {
  .job__filter {
    justify-content: flex-end;
  }
}
.job__filter > div:hover {
  background-color: #8fdbdb;
  transition: 0.4s ease;
}

@media (min-width: 46.25rem) {
  .job {
    display: grid;
    grid-template-columns: 1fr 3.5fr 3fr;
    grid-template-areas: "logo tag filter" "logo position filter" "logo detail filter";
  }
  .job__logo {
    grid-area: logo;
  }
  .job__tag {
    grid-area: tag;
  }
  .job__position {
    grid-area: position;
    align-self: center;
  }
  .job__detail {
    grid-area: detail;
  }
  .job__filter {
    grid-area: filter;
  }
}